@import "../../vars.scss";

.services {
    background-image: url("../../assets/img/Service/bg.png");
    background-size: cover;
    background-repeat: no-repeat;
    padding: 5% 13%;
    &__header {
        width: 100%;
        text-align: center;
        &-title {
            font-family: "Bebas Neue Book";
            font-size: 20px;
            line-height: 16px;
            letter-spacing: 20px;
            text-transform: uppercase;
            color: #fee040;
            margin-bottom: 20px;
            @media (max-width: $iPhoneX) {
                font-size: 16px;
                line-height: 12px;
            }
        }
        &-text {
            font-family: "Gilroy-ExtraBold";
            font-size: 32px;
            line-height: 42px;
            letter-spacing: -0.5px;
            margin-bottom: 20px;
            color: #ffffff;
            @media (max-width: $iPhoneX) {
                font-size: 20px;
                line-height: 16px;
            }
        }
    }
}
.slider {
    width: 100%;
    margin-right: 50px;
    @media (max-width: $iPhoneSE) {
        margin-right: 0px;
    }
    &__title {
        font-family: "Proxima Nova Condensed Thin";
        font-size: 24px;
        line-height: 28px;
        letter-spacing: -0.5px;
        color: #ffffff;
        margin-bottom: 20px;
        user-select: none;
        @media (max-width: $iPhoneX) {
            font-size: 16px;
            line-height: 12px;
        }
        @media (max-width: $iPhoneSE) {
            font-size: 12px;
            width: 40px;
        }
    }
    &__text {
        font-family: "Proxima Nova Condensed Thin";
        font-size: 16px;
        line-height: 26px;
        letter-spacing: -0.5px;
        color: #ffffff;
        opacity: 0.7;
        margin-bottom: 29px;
        user-select: none;
        width: 350px;
        @media (max-width: $iPhoneXR) {
            width: 170px;
        }
        @media (max-width: $iPhoneX) {
            width: 150px;
            font-size: 12px;
            line-height: 12px;
        }
        @media (max-width: $iPhoneSE) {
            font-size: 10px;
            width: 40px;
        }
    }
    &__details {
        font-family: "Bebas Neue Book";
        font-size: 20px;
        line-height: 16px;
        letter-spacing: 7px;
        color: #fee040;
        user-select: none;
        @media (max-width: $iPhoneX) {
            font-size: 12px;
            line-height: 12px;
        }
        @media (max-width: $iPhoneSE) {
            font-size: 8px;
            letter-spacing: 2px;
        }
    }
    &__details:hover {
        font-family: "Bebas Neue Book";
        font-size: 20px;
        line-height: 16px;
        letter-spacing: 7px;
        color: #fee040;
        user-select: none;
    }
    &__img {
        width: 350px;
        height: 190px;
        user-select: none;
        @media (max-width: $iPhoneXR) {
            width: 200px;
            height: 140px;
        }
        @media (max-width: $iPhoneX) {
            width: 150px;
            height: 140px;
        }
        @media (max-width: $iPhoneSE) {
            width: 40px;
            height: 20px;
        }
    }
}
