@import "../../vars.scss";

.steps {
    padding: 10% 0 0;
    @media (max-width: $iPhoneX) {
        padding: 5% 0 0;
    }
    &__title {
        font-family: "Bebas Neue Bold";
        font-size: 80px;
        line-height: 64px;
        text-align: center;
        letter-spacing: 5px;
        text-transform: uppercase;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        background: linear-gradient(rgba(96, 182, 255, 1), rgba(0, 78, 134, 0.8));
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        -webkit-text-stroke-width: 2px;
        -webkit-text-stroke-color: black;
        color: #0b2349;
        display: table;
        margin: 0 auto 36px;
        @media (max-width: $iPhoneX) {
            font-size: 48px;
            margin: 0 auto 20px;
        }
    }
    &__text {
        font-family: "Gilroy-ExtraBold";
        font-size: 20px;
        line-height: 28px;
        text-align: center;
        color: #505050;
        margin-bottom: 100px;
        @media (max-width: $iPhoneX) {
            font-size: 16px;
            line-height: 14px;
            margin-bottom: 30px;
        }
    }
    &__flex {
        display: flex;
    }
    &__padding {
        padding: 80px 70px;
        background: #f5f5f5;
        @media (max-width: $iPhoneX) {
        padding: 40px 20px;
        }
    }
    &__step {
        background: #fee040;
        color: #151515;
        font-family: "Proxima Nova Bold";
        font-size: 16px;
        line-height: 23px;
        letter-spacing: 3px;
        text-transform: uppercase;
        padding: 4px 10px;
        margin-right: 20px;
        margin-bottom: 20px;
        @media (max-width: $iPhoneX) {
            font-size: 12px;
            line-height: 12px;
            margin-right: 0px;
            padding: 2px 4px;
        }
    }
    &__nameStep {
        font-family: "Proxima Nova Bold";
        font-size: 24px;
        line-height: 34px;
        letter-spacing: -0.5px;
        color: #151515;
        margin-bottom: 20px;
        @media (max-width: $iPhoneX) {
            font-size: 16px;
            line-height: 14px;
            margin-bottom: 0px;
        }
    }
    &__textStep {
        font-family: "Proxima Nova Bold";
        font-size: 18px;
        line-height: 26px;
        letter-spacing: -0.5px;
        color: #333333;
        opacity: 0.7;
        margin-bottom: 25px;
        @media (max-width: $iPhoneX) {
            text-align: center;
            font-size: 14px;
            line-height: 14px;
            margin-bottom: 5px;
        }
    }
    &__item-step {
        margin-bottom: 18px;
    }
    &__item-text {
        font-family: "ProximaNova-RegularIt";
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.5px;
        color: #505050;
        opacity: 0.7;
        margin-bottom: 20px;
        @media (max-width: $iPhoneX) {
            text-align: center;
            font-size: 12px;
            line-height: 12px;
            margin-bottom: 5px;
        }
    }
    &__item {
        font-family: "Proxima Nova Bold";
        font-size: 16px;
        line-height: 26px;
        letter-spacing: -0.5px;
        color: #151515;
        opacity: 0.7;
        @media (max-width: $iPhoneX) {
            text-align: center;
            font-size: 12px;
            line-height: 12px;
        }
    }
    &__item-upp {
        text-transform: uppercase;
    }
    &__img {
        object-fit: cover;
        width: 50%;
    }
}
.steps__flex img:last-child{
    @media (max-width: $iPhoneX) {
    display: none;
    }
}
