@import "./vars.scss";

*,
*:after,
*:before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}
body {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 1;
  color: #111;
  background: #fff;
  overflow-x:hidden;
}

hr {
  border: none;
}

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

@font-face {
  font-family: 'Proxima Nova Condensed Thin';
  src: local('Proxima Nova Condensed Thin'),  url('./assets/fonts/proxima-nova-condensed-thin.ttf') format('truetype');
}
@font-face {
  font-family: 'Proxima Nova Bold' ;
  src: url('./assets/fonts/proxima-nova-bold.ttf');
}
@font-face {
  font-family: 'Proxima Nova Black';
  src: url('./assets/fonts/proxima-nova-bold.ttf');
}
@font-face {
  font-family: 'Gilroy-ExtraBold';
  src: url('./assets/fonts/Gilroy-ExtraBold.otf');
}
@font-face {
  font-family: 'Bebas Neue Book';
  src: url('./assets/fonts/bebasneuebook.ttf');
}
@font-face {
  font-family: 'Bebas Neue Bold';
  src: url('./assets/fonts/bebasneuebold.ttf');
}
@font-face {
  font-family: 'ProximaNova-RegularIt';
  src: url('./assets/fonts/ProximaNova-RegularIt.ttf');
}