@import "../../vars.scss";

.why {
    display: flex;
    @media (max-width: $iPhoneXR) {
        padding-top: 5%;
    }

    &__work {
        width: 25%;
        margin-left: auto;
        margin-right: auto;
        @media (max-width: $iPhoneXR) {
            width: 30%;
        }
    }
    &__work-gray {
        background: #f5f5f5;
        position: absolute;
        width: 50%;
        height: 41vw;
        top: 34vw;
        right: 0;
        z-index: -1;
        @media (max-width: $dekstop) {
            height: 70vw;
            top: 40vw;
        }
        @media (max-width: $iPhoneX) {
            top: 600px;
        }
    }
    &__work-title {
        font-family: "Gilroy-ExtraBold";
        font-size: 32px;
        letter-spacing: -0.5px;
        color: #151515;
        margin-bottom: 50px;
        line-height: 42px;
        @media (max-width: $iPhoneXR) {
            font-size: 24px;
            margin-bottom: 30px;
            line-height: 28px;
        }
        @media (max-width: $iPhoneX) {
            font-size: 16px;
            margin-bottom: 10px;
            line-height: 12px;
        }
    }
    &__work-text {
        font-family: "Proxima Nova Condensed Thin";
        font-size: 18px;
        letter-spacing: -0.5px;
        color: #707070;
        opacity: 0.7;
        line-height: 28px;
        @media (max-width: $iPhoneXR) {
            font-size: 16px;
            line-height: 20px;
        }
        @media (max-width: $iPhoneX) {
            font-size: 12px;
            margin-bottom: 10px;
            line-height: 12px;
        }
    }
    &__get {
        font-family: "Proxima Nova Black";
        font-size: 24px;
        line-height: 34px;
        letter-spacing: -0.5px;
        color: #151515;
        margin: 24px 0;
        @media (max-width: $iPhoneXR) {
            font-size: 24px;
            margin-bottom: 30px;
            line-height: 28px;
            margin: 0;
        }
        @media (max-width: $iPhoneX) {
            font-size: 16px;
            margin-bottom: 10px;
            line-height: 12px;
        }
    }
    &__get-item {
        font-family: "Proxima Nova Condensed Thin";
        font-size: 18px;
        line-height: 26px;
        letter-spacing: -0.5px;
        color: #333333;
        opacity: 0.7;
        margin-bottom: 19px;
        margin-left: 50px;
        @media (max-width: $iPhoneXR) {
            font-size: 16px;
            line-height: 20px;
            margin-bottom: 14px;
        }
        @media (max-width: $iPhoneX) {
            font-size: 12px;
            margin-bottom: 10px;
            line-height: 12px;
            margin-left: 0px;
        }
    }
    &__get-item::before {
        background-image: url("../../assets/img/Why/icon.png");
        content: " ";
        position: absolute;
        width: 30px;
        height: 30px;
        margin-left: -50px;
        @media (max-width: $iPhoneX) {
            display: none;
        }
    }
}
.why::before {
    background-image: url("../../assets/img/Why/tv.png");
    content: " ";
    background-repeat: no-repeat;
    background-size: contain;
    width: 300px;
    height: 294px;
    position: absolute;
    left: 41%;
    @media (max-width: $iPhoneXR) {
        height: 200px;
    }
    @media (max-width: $iPhoneX) {
        height: 100px;
    }
    @media (max-width: $iPhoneSE) {
        display: none;
    }
}
.why img {
    height: 25%;
    width: 25%;
}
