@import "../../vars.scss";

.header {
    width: 100%;
    padding: 2% 13%;
    z-index: 2;
    position: absolute;
    display: flex;
    justify-content: space-between;
    font-family: "Proxima Nova Condensed Thin";
    font-size: 12px;
    color: #ffffff;
    @media (max-width: $iPhoneX) {
        display: block;
        text-align: center;

    }
    @media (max-width: $iPhoneXR) {
        padding: 1% 5%;
    }
    &__logo {
        margin-left: 5vw;
        cursor: pointer;
        position: relative;
        top: -30px;
    }
    &__burger {
        position: absolute;
        cursor: pointer;
        background-image: url("../../assets/img/Header/burger.png");
        z-index: 3;
    }
    &__connect {
        display: flex;
        @media (max-width: $iPhoneX) {
            display: block;
        }
    }
    &__email {
        margin-right: 100px;
        @media (max-width: 1700px) {
            margin-right: 25px;
        }
    }
    &__email-title {
        opacity: 0.5;
        line-height: 12px;
        margin-bottom: 6px;
        @media (max-width: $iPhoneX) {
            margin-bottom: 0px;
        }
    }
    &__email-text {
        text-decoration: none;
        color: #fee040;
        font-size: 14px;

    }
    &__email-text:hover {
        text-decoration: underline;
        color: #fee040;
    }
    &__email-img {
        padding: 0 5px 2px 0;

    }
    &__phone {
        opacity: 0.5;
        line-height: 12px;
        text-align: right;
        @media (max-width: $iPhoneX) {
            text-align: center;
        }
    }
    &__number {
        font-family: "Proxima Nova Black";
        font-size: 20px;
        line-height: 24px;
        text-align: right;
        color: #ffffff;
        text-decoration: none;
    }
    &__number:hover {
        text-decoration: underline;
        color: #ffffff;
    }
}
