@import "../../vars.scss";

.intro {
    background-image: url("../../assets/img/Intro/bg.jpg");
    object-fit: cover;
    background-size: cover;
    background-repeat: no-repeat;
    padding: 130px 0 0 13%;
    color: #ffffff;
    @media (max-width: $iPhoneX) {
        padding: 200px 10% 0;
    }
    @media (max-width: $iPhoneSE) {
        padding: 200px 1% 0;
    }
    &__container {
        display: flex;
        padding: 0 0 0 5%;
        margin-bottom: 17%;
        @media (max-width: 1700px) {
            margin-bottom: 10%;
        }
        @media (max-width: $iPhoneX) {
            display: block;
            padding: 0;
            text-align: center;
        }
        @media (max-width: $iPhoneSE) {
            margin-bottom: 40%;
        }
    }
    &__main-title {
        font-family: "Gilroy-ExtraBold";
        font-size: 36px;
        line-height: 51px;
        margin-bottom: 38px;
        @media (max-width: $iPhoneXR) {
            font-size: 24px;
            line-height: 30px;
            margin-bottom: 20px;
            text-align: center;
        }
    }
    &__main-text {
        font-family: "Proxima Nova Condensed Thin";
        font-size: 20px;
        opacity: 0.7;
        @media (max-width: $iPhoneXR) {
            font-size: 16px;
            margin-bottom: 30px;
        }
    }
    &__main {
        margin-right: 7%;
    }
    &__button {
        border-radius: 2px;
        padding: 20px;
        background-color: #fee040;
        border: none;
        color: black;
        margin-bottom: 20px;
        font-size: 12px;
    }
    &__text {
        padding-right: 35%;
        font-size: 15px;
        line-height: 28px;
        opacity: 0.7;
        @media (max-width: $iPhoneX) {
            font-size: 12px;
            line-height: 14px;
            padding: 0 20%;
            text-align: center;
            // margin-bottom: 200px;
        }
    }
    &__list {
        display: flex;
    }
    &__item {
        margin-right: 5%;
        @media (max-width: $iPhoneXR) {
            font-size: 14px;
        }
        @media (max-width: $iPhoneX) {
            font-size: 12px;
        }
        @media (max-width: $iPhoneSE) {
            align-items: center;
        margin-right: 2%;
        }
    }
    &__list img {
        height: 45px;
        margin-right: 3%;
        @media (max-width: $iPhoneXR) {
            height: 35px;
        }
        @media (max-width: $iPhoneX) {
            height: 30px;
            margin-right: 2%;
        }

    }
    &__list a{
        @media (max-width: $iPhoneSE) {
            display: contents;
            height: 80px;
            top: -120px;
            left: -200px;
        }
    }
    &__list img:last-child {
        height: 220px;
        position: relative;
        top: -124px;
        left: -20px;
        @media (min-width: 1700px) {
            top: -224px;
            left: -100px;
        }
        @media (max-width: $iPhoneXR) {
            height: 150px;
            top: -80px;
            left: -20px;
        }
        @media (max-width: $iPhoneX) {
            height: 100px;
            top: -80px;
            left: -20px;
        }
        @media (max-width: $iPhoneSE) {
            height: 80px;
            top: -120px;
            left: -200px;
        }
    }
}
.Intro__item:last-child {
    margin: 0;
}
